import { Component, OnInit, Input, Output, OnDestroy } from '@angular/core';
import { EventEmitter } from 'events';
import { Router } from '@angular/router';
import { MyLessonFacade } from '../../mylesson-facade';
import { MyLessonInterface } from '../../../core/root-store/my-lesson-store';
import { Observable, Subject, combineLatest } from 'rxjs';
import { takeUntil, map, take } from 'rxjs/operators';

@Component({
  selector: 'app-card',
  templateUrl: './card.component.html',
  styleUrls: ['./card.component.scss']
})
export class CardComponent implements OnInit, OnDestroy {
  public destroy$: Subject<boolean> = new Subject<boolean>();
  @Input() data: MyLessonInterface;
  @Output() return = new EventEmitter();
  task: any = {};
  constructor(private router: Router, private myLessonFacade: MyLessonFacade) {}
  ngOnInit() {
    this.initiatCardData(this.data);
  }
  returnHandler() {
    // this.return.emit({});
  }
  taskClickHandler(item, grpName, i) {
    // this.studyStore.cardClickHandler(item, grpName, i);
  }

  launchLessonPlayer() {
    const config$ = this.setConfigByType(this.data);
    const resourceId$ = this.myLessonFacade.setSelectedResourceIdCallback();
    combineLatest(config$, resourceId$)
      .pipe(take(1), takeUntil(this.destroy$))
      .subscribe(() => {
        this.myLessonFacade.launchLessonPlayer(this.data.lessonType);
    });

    this.myLessonFacade.setSelectedResourceId(this.data.id);
  }

  setConfigByType(lessson: MyLessonInterface): Observable<void> {
    return new Observable(observer => {
      this.myLessonFacade
        .isConfigPresent(lessson.lessonType.toLowerCase())
        .pipe(takeUntil(this.destroy$))
        .subscribe(configData => {
          if (!configData) {
            this.myLessonFacade.loadAndSetConfig(lessson.lessonType.toLowerCase());
          } else {
            observer.next();
          }
        });
    });
  }

  initiatCardData(data) {
    this.task = {
      header: { label: data.chapterLabel, style: '' },
      subHeader: { label: data.lessonLabel, style: '' },
      supportingText: { label: data.duration + 'min', style: '' },
      type: { label: data.lessonType, style: '', data: {} },
      status: { data: 'overdue', style: '' },
      thumbnail: {
        data: { starCount: data.score },
        label: 'Math',
        style: 'subjThumb'
      }
    };
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.unsubscribe();
  }
}
