import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  SimpleChange,
  ElementRef,
  ViewChild
} from '@angular/core';
import { Subject, Observer } from 'rxjs';
import { Observable } from 'rxjs/internal/Observable';
declare var $: any;
declare var window: any;
@Component({
  selector: 'lib-progress-graph',
  templateUrl: './progress-graph.component.html',
  styleUrls: ['./progress-graph.component.css']
})
export class ProgressGraphComponent implements OnInit {
  generatedImage: any;
  image: string;
  @Input() opacity;
  @Input() tocStore;
  @Input() graphData: any = [];
  @Input() spData: any ;
  @Input() title;
  @Input() progressDay;
  isOpen = false;
  public updateMarker: Subject<any> = new Subject<any>();
  @Output() progressClicked = new EventEmitter();
  constructor() {}

  ngOnInit() {
    console.log(this.graphData);
  }
  getTime(data) {
    if (data.NR > 0) {
      return (data.NR / data.DN) * 100;
    }
    return 0;
  }
  getGoal(data) {
    if (data.NR > 0) {
      return (data.NR / data.DN) * 100;
    }
    return 0;
  }

  getLesson(data) {
    if (data.NR > 0) {
      return (data.NR / data.DN) * 100;
    }
    return 0;
  }
}
