import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { State } from './../core/root-store/root-store.state';
import {
  myLessonSelectors,
  myLessonActions,
  MyLessonInterface
} from '@root-store';
import { select, Store, ActionsSubject } from '@ngrx/store';
import { AppRoutingService } from './../app.routing.service';
import { ofType } from '@ngrx/effects';

@Injectable()
export class MyLessonFacade {
  constructor(
    private store: Store<State>,
    private actionsSubj: ActionsSubject,
    private appRoutingService: AppRoutingService
  ) {}

  isResourceLoaded(): Observable<boolean> {
    return this.store.pipe(select(myLessonSelectors.isResourceLoaded));
  }

  getLessonData(): Observable<MyLessonInterface[]> {
    return this.store.pipe(select(myLessonSelectors.getLessons));
  }

  getLessonDataApi() {
    this.store.dispatch(new myLessonActions.GetLessons());
  }

  isConfigPresent(lessonType: string) {
    return this.store.pipe(
      select(myLessonSelectors.getMyLessonConfigByType(), {
        lessonType: lessonType
      })
    );
  }

  loadAndSetConfig(lessonType: string) {
    this.store.dispatch(new myLessonActions.GetConfig(lessonType));
  }

  setSelectedResourceId(resourceId: string){
    this.store.dispatch(new myLessonActions.SetResource(resourceId));
  }

  setSelectedResourceIdCallback(): Observable<any> {
    return this.actionsSubj.pipe(
      ofType(myLessonActions.MyLessonsActionTypes.SET_RESOURCE)
    );
  }

  launchLessonPlayer(lessonType: string) {
    this.appRoutingService.launchLessonPlayer('myLesson', {
      player: lessonType.toLocaleLowerCase(),
      module: "myLesson",
    });
  }
}
