import { Component, OnInit, OnDestroy } from '@angular/core';
import { MyLessonFacade } from '../../mylesson-facade';
import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { HelperService } from '../../helper/help.service';
@Component({
  selector: 'app-lesson',
  templateUrl: './mylesson.component.html',
  styleUrls: ['./mylesson.component.scss']
})
export class MyLessonComponent implements OnInit, OnDestroy {
  spData = {
    task: {}
  };

  ring = [
    { label: 'Goal', NR: 55, DN: 100, Color: '#fff464' },
    { label: 'Minutes', NR: 77, DN: 100, Color: '#06d8f9' },
    { label: 'Lesson', NR: 66, DN: 100, Color: '#fe307b' },
    { label: 'Punctuality', NR: 45, DN: 100, Color: '#008000' }
  ];
  todayCard = [];
  futureCard = [];
  constructor(
    private location: Location,
    private myLessonFacade: MyLessonFacade,
    private helper: HelperService
  ) {}
  destroy$: Subject<boolean> = new Subject<boolean>();
  topicLabel;
  lessonList;
  isLessonPlayer = false;
  notePathArray = [
    'https://white-warrior.s3.ap-south-1.amazonaws.com/note-lib/122/note.json'
  ];

  ngOnInit() {
    // If Resource is empty
    this.myLessonFacade
      .isResourceLoaded()
      .pipe(takeUntil(this.destroy$))
      .subscribe((loaded: boolean) => {
        if (!loaded) {
          // Dispatch Api Call to get Resource List
          console.log(loaded);
          this.myLessonFacade.getLessonDataApi();
        }
      });

    // Fetching Resource Data
    this.myLessonFacade
      .getLessonData()
      .pipe(takeUntil(this.destroy$))
      .subscribe((lessons: any) => {
        this.initCardData(lessons);
      });
  }

  initCardData(data) {
    if (data.length > 0) {
      console.log(this.helper.chunkArray(data, 2));
      this.todayCard = this.helper.chunkArray(data, 2)[0];
      this.futureCard = this.helper.chunkArray(data, 2)[1];
    }
  }

  goBack() {
    this.location.back();
  }

  getSecToMin(duration) {
    return Math.round(duration / 60);
  }

  markCompleted(event) {
    this.isLessonPlayer = false;
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.unsubscribe();
  }
}
